import React from "react";
import { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Typography } from "@material-ui/core";
import ButtonBase from "@material-ui/core/CardActionArea";
import Box from "@material-ui/core/Box";
import theme from "../../themes/theme";
import { ThemeProvider } from "@material-ui/core/styles";
import FavoriteIcon from "@material-ui/icons/Favorite";
// import defaultMoviePoster from '../../assets/images/defaultMoviePoster.png';
import { useEffect } from "react";
import FloatCard from "../FloatCard/FloatCard";
import Axios from "axios";

const API_ADDRESS = "https://www.omdbapi.com/?apikey=e4c29baa&i=";

async function getMovieTitle(selectedTitle) {
  //recommendation

  const requestOptions = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(selectedTitle),
  };

  return fetch(
    "https://item-recommendation.herokuapp.com/recom",
    requestOptions
  )
    .then(async (response) => {
      const data = await response.json();
      // check for error response
      if (!response.ok) {
        // get error message from body or default to response status
        const error = (data && data.message) || response.status;
        return Promise.reject(error);
      }
      // console.log(data);
      return data;
    })

    .catch((error) => {
      console.error("There was an error!", error);
    });
}

const MovieCard = (props) => {
  // console.log(props);
  const [icon, setIcon] = useState(true);
  const [posterOpacity, setposterOpacity] = useState(true);
  const [iconOpacity, setIconOpacity] = useState(true);
  const [posterUrl, setPosterUrl] = useState(props.posterPath);

  let selectedList = props.selectedList;

  const useStyles = makeStyles((theme) => ({
    image: {
      maxWidth: "200px",
      maxHeight: "200px",
      minWidth: "200px",
      minHeight: "200px",
      borderRadius: 400,
      position: "absolute",
    },

    buttonbasestyle: {
      maxWidth: "200px",
      maxHeight: "200px",
      minWidth: "200px",
      minHeight: "200px",
      color: "transparent",
      borderRadius: 400,
      "&:hover": {
        color: "transparent",
      },
      marginBottom: 24,
      marginTop: 16,
    },

    iconStyle: {
      opacity: iconOpacity ? 0 : 1,
      position: "absolute",
      color: "red",
    },
    divStyle: {
      position: "relative",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      opacity: posterOpacity ? 1 : 0.7,
    },
    textStyle: {
      position: "absolute",
      top: 104,
      fontSize: 16,
      fontFamily: "Lato",
      color: icon ? theme.palette.text.primary : theme.palette.text.secondary,
    },
  }));

  const classes = useStyles();

  function onButtonClicked() {
    // console.log(props.title);

    setIcon(!icon);
    setposterOpacity(!posterOpacity);
    setIconOpacity(!iconOpacity);

    if (selectedList.includes(props.index)) {
      selectedList.splice(selectedList.indexOf(props.index), 1);
    } else {
      if (props.index < 62000) {
        selectedList.push(props.index);
        (async function () {
          let data = await getMovieTitle(props.index);
          await props.getDetails(data, props.index); //get details of predicted list here data is the predicted top 20
        })();
      }
    }

    props.showHide(selectedList);
  }

  useEffect(() => {
    //poster

    (async () => {
      let omdbRes;
      let posterPath;

      if (props?.Poster) {
        posterPath = props?.Poster;
      } else {
        try {
          omdbRes = await Axios.get(`${API_ADDRESS}${props.imdbId}`);
        } catch (err) {
          console.log(err);
        }

        try {
          const res = await Axios.get(
            "https://image.tmdb.org/t/p/w185" + props.posterPath
          );
          if (res.data === "<h1>File not Found</h1>") throw Error;
          posterPath = "https://image.tmdb.org/t/p/w185" + props.posterPath;
        } catch (err) {
          // console.log(res);
          posterPath = omdbRes?.data?.Poster ?? "Unknown";
        }
      }

      setPosterUrl(posterPath);
    })();
  }, [props.posterPath, props.imdbId]);

  useEffect(() => {
    if (selectedList.includes(props.index)) {
      setIcon(false);
      setposterOpacity(false);
      setIconOpacity(false);
    } else {
      setIcon(true);
      setposterOpacity(true);
      setIconOpacity(true);
    }
  }, [props.index, selectedList]);
  
  return (
    <ThemeProvider theme={theme}>
      <ButtonBase className={classes.buttonbasestyle} onClick={onButtonClicked}>
        <FloatCard>
          <Box className={classes.divStyle} textAlign="center">
            <img
              className={classes.image}
              src={posterUrl}
              alt=""
              onError={(e) => {
                e.target.onerror = null;
                e.target.src =
                  "data:image/png;base64,";
              }}
            />

            <Typography className={classes.textStyle}>{props.title}</Typography>
            <FavoriteIcon
              className={classes.iconStyle}
              fontSize="large"
            ></FavoriteIcon>
          </Box>
        </FloatCard>
      </ButtonBase>
    </ThemeProvider>
  );
};

export default MovieCard;
